




















































import Vue, {PropOptions} from "vue";
export default Vue.extend({
  name: "QLabel",
  data() {
    return {
      innerClass: "",
      mystyle: "white-space:pre-wrap"
    };
  },
  props: {
    text: {
      type: String,
      default: "Text",
      note: "Use to set label text of field"
    } as PropOptions,
    tagType: {
      type: String,
      default: "span",
      note: "Text tag type"
    } as PropOptions,
    maxWidth: {
      type: String
    },
    whiteSpace: {
      type: String,
      default: "pre-wrap"
    },
    textOverflow: {
      type: String
    },
    overflow: {
      type: String
    },
    processHtml:{
      type: Boolean,
      default: false
    }
  },
  mounted() {
    const self = this as unknown as {classes: Record<string, any> | undefined} // classes diye bişi var mı yok mu bilinmiyor. aşağıdaki satır komple anlamsız olabilir.
    if (self.classes) {
      this.innerClass = Object.keys(self.classes).join(" ");
    }
  },
  destroyed(){
   this.$el?.remove();
  },
  computed:{
    getType(){
      const self = <any>this;        
      if(self.processHtml){
        if(self.tagType == 'paragraph'){
          return 'paragraphWithHtml'
        }
        else if(self.tagType == 'span'){
          return 'spanWithHtml'
        }
      }
      return self.tagType
    }
  }
});
